export default {
	data() {
		return {
			phone: '',
			password: '',
			passType: 'password',
			isPhoneClear: false,
			isPassClear: false,
			
		};
		
		},
	methods:{
		backto:function(){
			uni.navigateBack({
				delta: 1
			});
		},
		showPass: function(){
			this.passType = this.passType==='password'?'text':'password'
		},
		clearPhone: function(){
			this.contPhone = ''
		},
		clearPass: function(){
			this.contPass =''
		},
		showCloseIcon:function(e){
			if (e.target.value){
				this.isPhoneClear = true
			}else {
				this.isPhoneClear = false
			}
		},
		showPassClearIcon: function(e){
			if(e.target.value) {
				this.isPassClear = true
			}else {
				this.isPassClear = false
			}
			
		},
		
		getValidCode:function(){
			uni.request({
				url: this.apiServer + '/ul/verification',
				method: 'POST',
				data: {
					phone:　this.phone
				},
				success: res => {
					uni.showToast({
						title: '验证码已发送',
						duration: 2000,
						icon: 'none'
					});
				},
				fail: () => {},
				complete: () => {}
			});
		},
		formSubmit: function(e){
			let data = e.detail.value;
			let code = data.code;
			delete data.code;
			console.log(data);
			
			if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(data.phone))){
				uni.showToast({
					title: '请输入正确电话号码！',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			if(!data.password || data.password.length<6){
				uni.showToast({
					title: '请输入至少六位密码',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			
			if(!code){
				uni.showToast({
					title: '验证码不能为空',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			uni.request({
				url: this.apiServer+'/ul/registration',
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				data: { 
					phone: data.phone,
					password: data.password,
					verification: code
				},
				success: res => {
					console.log(res.data);
					if (res.data.status  == 0){
						uni.switchTab({
							url: "/pages/qing-f-c/index"
						})
					}
				},
				fail: (err) => {
					console.log(err.data)
				},
				complete: () => {}
			});
			
		}
	
	}
}
